.section__padding{
  padding: 4rem 6rem;
}

.section__margin{
  margin: 4rem 6rem;
}

.app__wrapper{
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* min-height: 100vh; */
}

.app__wrapper_info{
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}


.app__wrapper_img{
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 2rem;
}

.app__wrapper_img-reverse {
  justify-content: flex-start;
  margin-right: 2rem;
}


.app__wrapper_img img{
  width: 80%;
}

.tagline{
color: var(--color-orange);
/* font-family: Poppins; */
font-size: 20px;
font-style: normal;
font-weight: 700;
line-height: normal;
text-transform: uppercase;
margin-bottom: 1rem;
}

.subheading{
  color:  #5E6282;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 124.5%; /* 19.92px */
}

.heading{
color:  #181E4B;
/* font-family: Volkhov; */
font-size: 84px;
font-style: normal;
font-weight: 700;
line-height: 89px; /* 105.952% */
letter-spacing: -3.36px; 
margin-bottom: 1rem;
}

.title{
text-align: center;
font-size: 50px;
font-style: normal;
font-weight: 700;
line-height: normal;
text-transform: capitalize;
margin-bottom: 2rem; 
}

.description{
  color: #5E6282;
/* font-family: Poppins; */
font-size: 16px;
font-style: normal;
font-weight: 500;
line-height: 30px; /* 187.5% */
margin-top: 1rem;
margin-bottom: 1rem;
}

.custom-button{  
cursor: pointer; 
border: none;
border-radius: 10px;
background: var(--color-yellow);
box-shadow: 0px 20px 35px 0px rgba(241, 165, 1, 0.15);
padding: 0.8rem 1.5rem;
color: var(--color-white);
text-align: center;
font-size: 16px;
letter-spacing: -0.04em;
font-style: normal;
font-weight: 500;
line-height: normal;
margin-top: 1rem;
}


@media screen and (max-width: 1050px) {
  .heading{
    font-size: 64px;
    line-height: 69px; /* 105.952% */
    margin-bottom: 1rem;
    }
  
}

@media screen and (max-width: 850px){
  .section__padding{
    padding: 4rem;
  }

  .section__margin{
    margin: 4rem;
  }
  

  .app__wrapper{
    flex-direction: column;
  }

  .app__wrapper_img, .app__wrapper_card{
    margin-left: 0rem;
    margin-top: 4rem;
  }

}

@media screen and (max-width: 650px){
  .section__padding{
    padding: 4rem 2rem;
  }

  .section__margin{
    margin: 4rem 2rem;
  }
  

  .title{
    font-size: 40px;
    }


}

@media screen and (max-width: 450px) {
  

  .title{
    font-size: 36px;
  }

  .description{
    font-size: 14px;
    line-height: 24px;
  }
  
}
